#root {
   width: 100%;
}
.wrapper{
   height: 100%;
   width: 100%;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
}
.main {
   flex: 1 1 auto;
}
.page {
   width: 100%;
   display: flex;
   justify-content: center;
}
.animated__page {
   transform: translateX(-100%);
   transition: transform 0.5s linear;
}
.container{
   width: 95%;
   display: flex;
   flex-direction: column;
   align-items: center;
}
.body {
   display: flex;
   justify-content: space-between;
   width: 100%;
}
.content {
   width: 100%;
   padding: 3.5% 0;
   @media screen and (max-width: 425px) {
      padding: 8% 0;
   }
}
.center {
   text-align: center;
   margin-bottom: 30px;
}